import RightBar from './right-bar';
import ConfirmStart from './confirm-start';
import ConfirmMember from './confirm-member';
import ShowScore from './publish-score';
import TeamGrouping from './team-grouping';


export default {
  name: 'PressHorScheduleManage',
  options: {
    styleIsolation: 'shared',
  },
  components: {
    RightBar,
    ConfirmStart,
    ConfirmMember,
    ShowScore,
    TeamGrouping,
  },
  props: {
    tabIndex: {
      type: Number,
      default: 0,
    },
    curGoingTab: {
      type: Number,
      default: 0,
    },
    teamMap: {
      type: Object,
      default: () => ({}),
    },
    searchTeamMap: {
      type: Object,
      default: () => ({}),
    },
    isAllConfirmed: {
      type: Boolean,
      default: false,
    },
    teamTotal: {
      type: Number,
      default: 0,
    },
    checkedTeamTotal: {
      type: Number,
      default: 0,
    },
    numPerGroup: {
      type: Number,
      default: 1,
    },
    toSelectNumList: {
      type: Array,
      default: () => [1],
    },
    groupNum: {
      type: Number,
      default: 0,
    },
    groupListMap: {
      type: Object,
      default: () => ({}),
    },
    // groupList: {
    //   type: Array,
    //   default: () => ([]),
    // },
    roundTotal: {
      type: Number,
      default: 1,
    },
    groupMap: {
      type: Object,
      default: () => ({}),
    },
    searchTeamMapInGroup: {
      type: Object,
      default: () => ({}),
    },
    curStartRoundId: {
      type: Number,
      default: 1,
    },
    curScoreRoundId: {
      type: Number,
      default: 1,
    },
    teamScoreMap: {
      type: Object,
      default: () => ({}),
    },
    searchTeamListInGroup: {
      type: Array,
      default: () => ([]),
    },
    roundList: {
      type: Array,
      default: () => ([]),
    },
    publishedScore: {
      type: Boolean,
      default: false,
    },
  },
  emits: [
    'changeScoreRoundId',
    'changeStartRoundId',
    'changeTab',
    'checkTeam',
    'clickSearch',
    'close',
    'confirmAdjustGroup',
    'doneConfirmSignUp',
    'loadMore',
    'publishScore',
    'search',
    'searchTeamGroup',
    'selectGroupInScore',
    'selectNumPerGroup',
    'startAllGame',
    'startGame',
    'toggleAllQualification',
    'update:loading',
  ],
  data() {
    return {
      barList: ['确认资格', '队伍分组', '确认开赛', '公布成绩'],
      isSearch: false,
    };
  },
  created() {
  },
  mounted() {

  },
  methods: {
    close() {
      this.$emit('close');
    },
    changeTab(index) {
      this.$emit('changeTab', index);
    },
    toggleAllQualification() {
      this.$emit('toggleAllQualification');
    },
    doneConfirmSignUp() {
      this.$emit('doneConfirmSignUp');
    },
    checkTeam(team, index) {
      this.$emit('checkTeam', team, index);
    },
    selectNumPerGroup(num) {
      this.$emit('selectNumPerGroup', num);
    },
    confirmAdjustGroup() {
      this.$emit('confirmAdjustGroup');
    },
    changeStartRoundId(roundId) {
      this.$emit('changeStartRoundId', roundId);
    },
    changeScoreRoundId(roundId) {
      this.$emit('changeScoreRoundId', roundId);
    },
    startGame(item) {
      this.$emit('startGame', item);
    },
    startAllGame() {
      this.$emit('startAllGame');
    },
    publishScore() {
      this.$emit('publishScore');
    },
    onSearch(value) {
      this.$emit('search', value);
    },
    onClickSearch() {
      this.isSearch = true;
      this.$emit('clickSearch');
    },
    searchTeamGroup(value) {
      this.$emit('searchTeamGroup', value);
    },
    selectGroupInScore(value) {
      this.$emit('selectGroupInScore', value);
    },
    loadMore(key) {
      this.$emit('loadMore', key);
    },
    updateLoading(key, value) {
      this.$emit('update:loading', key, value);
    },
  },
};
